.menu-trigger {
  position: absolute;
  top: 0;
  right: 24px;
  cursor: pointer;
  z-index: 10000;
  padding: 20px;
}

.menu-trigger span {
  display: inline-block;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  background-color: #000;
  transition: 0.8s transform;
  position: relative;
  z-index: 10000;
  color: #fff;
}

.menu-trigger span:nth-child(2) {
  margin: 0 6px;
}

.menu-trigger.menu-is-open span {
  background-color: #fff;
  height: 8px;
  width: 8px;
}

.menu-trigger.menu-is-open span:nth-child(2),
.menu-trigger.menu-is-open span:nth-child(3) {
  display: none;
}

.menu-trigger:hover span:nth-child(1) {
  animation: bounce 0.7s;
}

.menu-trigger:hover span:nth-child(2) {
  animation: bounce 0.7s;
  animation-delay: 0.2s;
}

.menu-trigger:hover span:nth-child(3) {
  animation: bounce 0.7s;
  animation-delay: 0.2s;
}

@keyframes bounce {
  from,
  20%,
  53%,
  80%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }
  40%,
  43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -2px, 0);
  }
  70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function-transform: translate3d(0, 0, 0);
  }
  90% {
    transform: translate3d(0, 2px, 0);
  }
}

